import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import AddButton from "../../../Ui/Button/AddButton";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import { customDebounce } from "../../../Utils/Functions/debounce";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import style from "../../FinanceManager/FinanceManager.module.css";
import Loading from "../../../Loader/Loading";
import QvipleLoader from "../../../Loader/QvipleLoader";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import {
  useLmsAddExcelBiometricStaff,
  useLmsBiometricStaffList,
} from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import UploadExcelButton from "../../../Ui/Button/UploadExcelButton";
import { useFileUpload } from "../../../Hooks/ApiHooks/Auth/auth-api";
import LmsBiometricCard from "./LmsBiometricCard";
import LmsBiometricAddStaff from "./LmsBiometricAddStaff";

const LmsBiometric = ({ lmid, instituteId }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [add, setAdd] = useState(false);
  const [disabled, setDisabled] = useState(false);
  const [fileUpload] = useFileUpload();
  const [lmsAddExcelBiometricStaff] = useLmsAddExcelBiometricStaff();

  const {
    lmsBiometricStaffList,
    lmsBiometricStaffListLoading,
    lmsBiometricStaffListRefetch,
  } = useLmsBiometricStaffList({
    data: {
      lmid: lmid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !lmid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (lmsBiometricStaffList?.list?.length) {
      let obj = nextPage(lmsBiometricStaffList?.list?.length);
      setState(() => obj);
    }
  }, [lmsBiometricStaffList?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  const onRefetch = useCallback(() => {
    setPage(() => 1);
    setSearch("");
    lmsBiometricStaffListRefetch();
  }, [lmsBiometricStaffListRefetch]);

  const onExcelFileUpload = (e) => {
    if (e.target.files[0]) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey) {
            lmsAddExcelBiometricStaff({
              lmid: lmid,
              excelFile: {
                excel_file: res.data?.imageKey,
              },
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("biometric")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
              <UploadExcelButton onExcelFileUpload={onExcelFileUpload} />
              <AddButton
                label="add_biometric"
                onAction={onToggle}
                customStyle={{
                  padding: "0.4rem 1rem",
                }}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          {lmsBiometricStaffList?.list?.map((staff, index) =>
            lmsBiometricStaffList?.list?.length === index + 1 ? (
              <div key={staff?._id} ref={ref}>
                <LmsBiometricCard staff={staff} />
              </div>
            ) : (
              <LmsBiometricCard staff={staff} key={staff?._id} />
            )
          )}
          {lmsBiometricStaffListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            lmsBiometricStaffList?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No staff fuond related this search."
              />
            )
          ) : (
            lmsBiometricStaffList?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No staff added."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {add && (
        <LmsBiometricAddStaff
          instituteId={instituteId}
          onClose={onToggle}
          onRefetch={onRefetch}
        />
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default LmsBiometric;
