import { useTranslation } from "react-i18next";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import style from "./Button.module.css";
const UploadExcelButton = ({
  onExcelFileUpload,
  acceptType,
  label,
  dynamicKey,
  title,
}) => {
  const { t } = useTranslation();
  return (
    <>
      <label
        className={style.upload_excel_file}
        htmlFor={dynamicKey ?? "uploadExcel"}
      >
        <img
          src={`${assestsUserFinanceUrl}/upload-xslx.svg`}
          alt="add student icon"
          title={title ?? "Upload excel file"}
        />
        {label && <h6>{t(label)}</h6>}
      </label>

      <input
        type={"file"}
        id={dynamicKey ?? "uploadExcel"}
        accept={acceptType}
        onChange={onExcelFileUpload}
        style={{
          display: "none",
        }}
      />
    </>
  );
};

export default UploadExcelButton;
