import { useState } from "react";
import InstituteStaff from "../../InstituteStaff/InstituteStaff";
import { useLmsAddBiometricStaff } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import CreateButton from "../../../Ui/Button/CreateButton";
import {
  existingRequiredField,
  requiredField,
} from "../../../Validation/functions/checkingRequiredField";
import style from "../../FinanceManager/Moderator/FinanceModerator.module.css";
import { useTranslation } from "react-i18next";
import FormInput from "../../../Ui/Input/FormInput";

const LmsBiometricAddStaff = ({ instituteId, onRefetch, onClose }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [staffList, setStaffList] = useState("");
  const [state, setState] = useState("");
  const [errorField, setErrorField] = useState("");

  const [lmsAddBiometricStaff] = useLmsAddBiometricStaff();
  const onSelect = (val) => {
    setStaffList(val);
  };

  const onAddBiometric = () => {
    let validation = requiredField(
      {
        machineId: state,
      },
      []
    );

    if (existingRequiredField(validation) && staffList) {
      setDisabled((pre) => !pre);
      lmsAddBiometricStaff({
        addBiometric: {
          sid: staffList?._id ?? "",
          mcid: state,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };
  return (
    <>
      {!staffList ? (
        <InstituteStaff
          title="add_staff_biometric"
          onClose={onClose}
          instituteId={instituteId}
          selectedId={staffList?._id}
          onSelectStaff={onSelect}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("add_staff_biometric")}</h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <section className={style.selected_staff_container}>
                <img
                  src={
                    staffList?.staffProfilePhoto
                      ? `${imageShowUrl}/${staffList?.staffProfilePhoto}`
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                  alt="staff profile avatar"
                />
                <div className={style.selected_staff_text}>
                  <h6>
                    {`${staffList?.staffFirstName ?? ""} ${
                      staffList?.staffMiddleName ?? ""
                    } ${staffList?.staffLastName ?? ""}`}
                  </h6>
                  <p>
                    {t("index_number")} {": "}
                    {staffList?.staffROLLNO ?? ""}
                  </p>
                </div>
              </section>

              <FormInput
                label={t("staff_machine_id")}
                placeholder={t("staff_machine_id_placeholder")}
                name={"machineId"}
                value={state}
                type={"text"}
                onChange={(e) => setState(e.target.value)}
                errorShow={errorField["machineId"]}
              />

              <CreateButton label="add" onAction={onAddBiometric} />
            </UserMemberContentWrapper>
          </div>
        </Modal>
      )}

      {disabled && <QvipleLoader />}
    </>
  );
};

export default LmsBiometricAddStaff;
